.badge {
  padding-left: 9px;
  padding-right: 9px;
  border-radius: 9px;
}

.label-warning[href],
.badge-warning[href] {
  background-color: #c67605;
}
#lblCartCount {
  font-size: 12px;
  background: transparent;
  color: white;
  padding: 0 5px;
  vertical-align: top;
  margin-left: -10px;
}
@media (min-width: 996px) {
  #lblCartCount {
    font-size: 12px;
    color: black;
    padding: 0 5px;
    vertical-align: top;
    margin-left: -10px;
  }

}

