.footer {
  max-height: 0px;
  overflow:hidden;
  transition: all 300ms ease-out;

}

.footer-enter {
  max-height: 400px;
  transition: all 300ms ease-out;
}
.footer-enter-active {
  max-height: 400px;

  transition: all 300ms ease-out;
}
.footer-exit {
  max-height: 200px;

  transition: all 300ms ease-out;

}
.footer-exit-active {
  overflow:hidden;

  max-height: 0px;

}


